.worddetails {
    &__title {
        font-family: "Nunito";
        font-weight: 700;
        font-size: 8em;
    }
    &__inner {
        display: grid;
        grid-template-columns: 50% 50%;
        align-items: center;
    }
    &__img {
        padding-right: 100px;
    }
    &__change {
        position: relative;
        &-input {
            margin-bottom: 20px;
            width: 100%;
            label {
                width: 100%;
                position: relative;
                padding: 20px;
                padding-left: 60px;
                background-color: #ecf8ff;
                display: flex;
                align-items: center;
                img {
                    position: absolute;
                    width: 40px;
                    left: 10px;
                }
            }
            input {
                width: 90%;
                padding: 10px;
                outline: none;
                border-radius: 3px;
                border: 1px solid #b0b0b0;
                font-size: 18px;
                &::placeholder {
                    font-size: 18px;
                    color: black;
                }
            }
        }
        &-button {
            position: absolute;
            right: 0;
            background: none;
            outline: none;
            border: none;
            text-align: right;
            color: #0f6abe;
            font-size: 18px;
            cursor: pointer;
        }
    }
}
