.events {
    border-radius: 12px;
    overflow: hidden;
    width: 100%;
    background: linear-gradient(180deg, rgba(19, 154, 230, 0.2) 0%, rgba(29, 148, 216, 0) 100%);
    &__top {
        background-color: #2ea3e6;
        padding: 0 30px;
        color: white;
        font-size: 28px;
        margin-bottom: 20px;
    }
    &__items {
        position: relative;
        &::after {
            content: "";
            pointer-events: none;
            display: block;
            height: 30%;
            position: absolute;
            bottom: 0;
            left: 0;
            right: 0;
            background: linear-gradient(180deg, rgba(white, 0.2) 0%, rgba(white, 1) 100%);
        }
    }
    &__item {
        display: flex;
        padding: 15px 30px;
        &-time {
            color: #878787;
            margin-right: 19px;
        }
        &-text {
            font-size: 18px;
        }
    }
}
