@import "./assets/style/fonts.scss";
* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}
img {
    width: 100%;
}
a {
    text-decoration: none;
    color: white;
}

body {
    font-family: "Sarala";
    overflow-y: scroll;
    zoom: 75%;
}

.wrapper {
    padding: 50px 100px;
}

.search {
    padding: 2px 10px;
    font-size: 20px;
    font-weight: 700;
    border-radius: 4px;
    outline: none;
    border: 1px solid #858585;
    &::placeholder {
        color: #c9c9c9;
    }
    &[type="text"] {
        font-family: "Sarala";
        font-weight: 400;
    }
}
