.dashboard {
    &__inner {
        display: flex;
        justify-content: space-evenly;
        align-items: flex-start;
    }
    &__left {
        width: 60%;
        margin-right: 10px;
    }
    &__right {
        width: 40%;
        display: flex;
        flex-direction: column;
        &-top {
            display: flex;
            width: 100%;
            margin-bottom: 10px;
        }
    }
}


