.userdetails {
    &__top {
        display: flex;
        justify-content: space-between;
        margin-bottom: 50px;
    }
    &__username {
        color: #7e7e7e;
        line-height: 34px;
        &-title {
            font-weight: 700;
            color: #565656;
            font-size: 18px;
        }
    }
    &__info {
        &-language,
        &-answers {
            color: white;
            padding: 10px 50px 10px 15px;
            background-color: black;
            margin-bottom: 10px;
            border-radius: 12px;
        }
        &-language {
            background-color: #1990d3;
            position: relative;
            display: flex;
            align-items: center;
            &.ru {
                &::after {
                    content: '';
                    position: absolute;
                    display: inline-block;
                    right: 20px;
                    background-image: url('../../../assets/img/rus.svg');
                    background-position: center;
                    background-repeat: no-repeat;
                    background-size: cover;
                    width: 34px;
                    height: 24px;
                }
            }
        }
        &-answers {
            background-color: rgba(#40bf73, 0.2);
            position: relative;
            overflow: hidden;
            &-progress {
                position: absolute;
                top: 0;
                left: 0;
                background-color: #40bf73;
                height: 100%;
                z-index: -1;
            }
        }
    }
    &__box {

    }
    &__sort {
        display: grid;
        grid-template-columns: 33% 33% 33%;
        padding: 10px 0px 10px 100px;
        border-bottom: 2px solid #979797;
        color: #646464;
        font-weight: 700;
        &-answers, &-date {
            cursor: pointer;
            &::after {
                content: "";
                display: inline-block;
                width: 16px;
                height: 13px;
                background-image: url("../../../assets/img/arrow.svg");
                background-position: center;
                background-repeat: no-repeat;
                background-size: cover;
                margin-left: 5px;
                transition: all .3s ease-in-out;
            }
        }
    }
    &__items {

    }
    &__item {
        display: grid;
        grid-template-columns: 100px calc(33% - 30px) calc(33% - 30px) 33%;
        align-items: center;
        padding: 10px 0 10px 0;
        &-img {
            width: 90px;
        }
        &.disabled {
            * {
                opacity: .5;
            }
        }
    }
}
