.language {
    width: 100%;
    background: rgba(234, 162, 23, 0.1);
    border-radius: 12px;
    overflow: hidden;
    margin-right: 10px;
    &__inner {
        padding: 20px 20px;
    }
    &__top {
        background: #EAA217;
        padding: 0 30px;
        color: white;
        font-size: 28px;
    }
    &__items {
        display: flex;
        flex-wrap: wrap;
        justify-content: center;
        padding: 10px;
    }
    &__item {
        display: flex;
        width: calc(100% / 3);
        margin-bottom: 10px;
        &-img {
            margin-right: 20px;
            width: 50px;
        }
        &-count {
            line-height: 44px;
            font-family: 'Nunito';
            font-weight: 600;
        }
    }
}