.statistics {
    width: 100%;
    background: rgba(62, 165, 103, 0.1);
    border-radius: 12px;
    overflow: hidden;
    margin-left: 10px;
    &:first-child {
        margin: 0;
    }
    &__inner {
        padding: 5px 10px;
    }
    &__top {
        background: #209e52;
        padding: 0 30px;
        color: white;
        font-size: 28px;
    }
    &__count {
        font-size: 50px;
        line-height: 82px;
        font-weight: 700;
        color: #209e52;
        text-align: center;
    }
    &__text {
        text-align: center;
        color: #40bc71;
    }
}