.header {
    background-color: #0c8fd9;
    &__inner {
        display: flex;
        align-items: center;
        justify-content: space-between;
        padding-top: 10px;
        padding-right: 90px;
        padding-left: 90px;
    }
    &__logo {
        display: flex;
        align-items: center;
        img {
            margin-right: 35px;
        }
        a {
            font-family: "Schoolbell";
            font-size: 60px;
        }
    }
    &__menu {
        list-style: none;
        display: flex;
        li {
            padding: 0;
            a {
                font-size: 28px;
                margin: 0 15px;
                font-family: "Nunito";
                &.active {
                    font-weight: 700;
                    border-bottom: 2px solid white;
                }
            }
        }
    }
}
